* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  color: #5D6063;
  background-color: #EAEDF0;
  font-family: "Helvetica", "Arial", sans-serif;
  font-size: 16px;
  line-height: 1.3;

  display: flex;
  flex-direction: column;
  align-items: center;
}
  /*------------------------------------------------------------------------------------------- */

.speaker-form-header {
  text-align: center;
  background-color: #F6F7F8;
  border: 1px solid #D6D9DC;
  border-radius: 3px;
  
  width: 80%;
  margin: 40px 0;
  padding: 50px;
}

.speaker-form-header h1 {
  font-size: 30px;
  margin-bottom: 20px;
}

.speaker-form {
  background-color: #F6F7F8;
  border: 1px solid #D6D9DC;
  border-radius: 3px;
  
  width: 80%;
  padding: 50px;
  margin: 0 0 40px 0;
}
.form-row {
  margin-bottom: 40px;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  flex-wrap: wrap;
}

.form-row input[type='text'],
.form-row input[type='email'],
.form-row input[type='date'],
  .form-row input[type='number']   {
  background-color: #FFFFFF;
  border: 1px solid #D6D9DC;
  border-radius: 3px;
  width: 100%;
  padding: 7px;
  font-size: 14px;
}
.form-row input[type='text']:invalid,
.form-row input[type='email']:invalid,
  .form-row input[type='number']   {
border: 1px solid #D55C5F;
color: #D55C5F;
box-shadow: none; /* Remove default red glow in Firefox */
}
.form-row label {
  margin-bottom: 15px;
}
/*------------------------------------------------------------------------------------------- */

.legacy-form-row {
  border: none;
  margin-bottom: 40px;
}

.legacy-form-row legend {
  margin-bottom: 15px;
}

.legacy-form-row .radio-label {
  display: block;
  font-size: 14px;
  padding: 0 20px 0 10px;
}

.legacy-form-row input[type='radio'] {
  margin-top: 2px;
}
  
.legacy-form-row .radio-label,
.legacy-form-row input[type='radio'] {
  float: left;
}
/*------------------------------------------------------------------------------------------- */
.form-row select {
  width: 100%;
  padding: 5px;
  font-size: 14px;            /* This won't work in Chrome or Safari */
  -webkit-appearance: none;   /* This will make it work */
}
  /*------------------------------------------------------------------------------------------- */
  .form-row textarea {
      font-family: "Helvetica", "Arial", sans-serif;
      font-size: 14px;
    
      border: 1px solid #D6D9DC;
      border-radius: 3px;
    
      min-height: 200px;
      margin-bottom: 10px;
      padding: 7px;
      resize: none;
    }
    
    .form-row .instructions {
      color: #999999;
      font-size: 14px;
      margin-bottom: 30px;
    }
  /*------------------------------------------------------------------------------------------- */
  .form-row .checkbox-label {
      margin-bottom: 0;
    }

        /*------------------------------------------------------------------------------------------- */
        .form-row button {
          font-size: 16px;
          font-weight: bold;
        
          color: #FFFFFF;
          background-color: #5995DA;
        
          border: none;
          border-radius: 3px;
        
          padding: 10px 40px;
          cursor: pointer;
        }
        
        .form-row button:hover {
          background-color: #76AEED;
        }
        
        .form-row button:active {
          background-color: #407FC7;
        }
.warning{
  color: red;
  font-style: italic;
}
      /*------------------------------------------------------------------------------------------- */

